
body
{
    background-color: white;
}

.AppAbout{
    background-color: white;
    font-family: 'Poppins', sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    position: absolute;
    width: 90%;
    top: 60px;
    overflow-y: hidden;
}

.about{
    text-align: center;
    flex-direction: column;
    background-color: white;
    box-shadow: rgb(17 12 46 / 15%) 0px 0px 10px 10px;
    border-radius: 20px;
    padding: 40px;
    margin-left: 20px;
    padding-bottom: 20px;
}

.about h1{
    padding-bottom: 15px;
}

#visual{
    top: -28em;
    width: 110em;
    height: 110em;
    position: absolute;
    z-index: -1;
}

.Members{
    display: flex;
    flex-direction: column;
    padding-bottom: 5px;
    color: white;
    background-color: #00dba4;
    box-shadow: rgb(17 12 46 / 15%) 0px 0px 15px 15px;
    border-radius: 30px;
    padding: 20px;
    margin: 20px;
    position: relative;
}

.Members h3{
    text-align: center;
    padding-bottom: 8px;
}

.member{
    display: flex;
    min-width: 360px;
    height: 150px;
}

.memberImg{height: 100%;}
.memberImg img{
    border-radius: 20%;
    height: 95%;
}

.memberAbout{
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}

.Name{
    font-size: larger;
    font-weight: 500;
}

.Job{
    font-weight: 300;
    border-bottom: solid 1px rgba(0, 0, 0, 0.11);
    margin-bottom: 10px;
}

.Desc{
    font-size: smaller;
}

.Contact{
    display: flex;
    flex-direction: row;
    position: relative;
    top: -12px;
    gap: 10px;
}

.Contact a{
    text-decoration: none;
    color: #00a97f;
}

.REE{
    padding-top: 10px;
    text-align: center;
    width: 80%;
}

@media (max-width: 1200px) {
    .Container{
        top: 80px;
        background-color: white;
        width: 100%;
        overflow-x: hidden;
        flex-direction: column;
    }
    .about{
        margin: 20px;
        margin-bottom: 0;
        min-width: 360px;
        max-width: 440px;
    }
    #aboutIntro
    {
        flex-direction: column;
        justify-content: center;
    }
}
